@import "../abstracts/variables";

.leaderboard-container {
  position: relative;
  min-height: 100vh;
  background: var(--bg-color);
  transition: all 0.3s ease;
  padding-top: 60px; // Add padding for navbar height
  margin-left: var(--sidebar-width);
  width: calc(100% - var(--sidebar-width));

  @media (max-width: 992px) {
    margin-left: 0;
    width: 100%;
  }


  @media (min-width: 992px) {
    margin-left: 260px;
  }

  .leaderboard-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;

    @media (max-width: 992px) {
      padding: 1rem;
    }
  }

  .dashboard-top-section {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 2rem;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;

    .column-main {
      width: 100%;
      
      .referral-container {
        background: var(--card-bg);
        border-radius: var(--border-radius);
        padding: 1.5rem;
        box-shadow: var(--shadow-sm);
        width: 100%;
      }
    }

    .column-side {
      width: 100%;
      
      .friend-requests-container {
        background: var(--card-bg);
        border-radius: var(--border-radius);
        padding: 1.5rem;
        box-shadow: var(--shadow-sm);
        width: 100%;
      }
    }
  }

  .search-bar {
    max-width: 800px;
    margin: 0 auto 2rem;
    
    input {
      background: var(--card-bg);
      border: 1px solid var(--border-color);
      color: var(--text-color);
      
      &:focus {
        border-color: var(--accent-color);
        box-shadow: 0 0 0 0.2rem rgba(var(--accent-color-rgb), 0.25);
      }
    }
  }

  .leaderboard-title {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--text-color);
    font-size: 2rem;
    font-weight: 600;
  }

  .leaderboard-toggle-container {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;

    .leaderboard-toggle {
      .btn {
        padding: 0.5rem 2rem;
        font-weight: 500;
        
        &.active {
          background-color: var(--accent-color);
          border-color: var(--accent-color);
        }
      }
    }
  }

  .table-responsive {
    max-width: 800px;
    margin: 0 auto;
  }

  .leaderboard-table {
    background: var(--card-bg);
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: var(--shadow-sm);

    .table-header {
      background: var(--card-bg);
      border-bottom: 2px solid var(--border-color);
      
      th {
        color: var(--text-color);
        font-weight: 600;
        padding: 1rem;
      }
    }

    tbody {
      tr {
        border-bottom: 1px solid var(--border-color);
        transition: all 0.2s ease;

        &:hover {
          background: rgba(var(--accent-color-rgb), 0.05);
        }

        td {
          padding: 1rem;
          color: var(--text-color);
          vertical-align: middle;
        }

        &.rank-gold {
          background: linear-gradient(45deg, rgba(255, 215, 0, 0.1), rgba(255, 215, 0, 0.05));
          td:first-child::before {
            content: '🥇';
            margin-right: 0.5rem;
          }
        }

        &.rank-silver {
          background: linear-gradient(45deg, rgba(192, 192, 192, 0.1), rgba(192, 192, 192, 0.05));
          td:first-child::before {
            content: '🥈';
            margin-right: 0.5rem;
          }
        }

        &.rank-bronze {
          background: linear-gradient(45deg, rgba(205, 127, 50, 0.1), rgba(205, 127, 50, 0.05));
          td:first-child::before {
            content: '🥉';
            margin-right: 0.5rem;
          }
        }
      }
    }
  }
}

.referral-container,
.friend-requests-container {
  background: var(--card-bg);
  border-radius: var(--border-radius-lg);
  padding: map-get($spacers, 4);
  box-shadow: var(--shadow-sm);
  min-width: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  
  @include media-breakpoint-up(xxl) {
    max-width: 700px;
  }
  
  @include media-breakpoint-between(md, lg) {
    padding: map-get($spacers, 3);
    max-width: 100%;
    height: 100%;
  }
  
  @include media-breakpoint-down(md) {
    padding: map-get($spacers, 3);
    max-width: 100%;
  }
}

.search-bar {
  margin: map-get($spacers, 5) auto;
  max-width: 600px;
  
  @include media-breakpoint-down(lg) {
    margin: map-get($spacers, 4) auto;
  }
  
  @include media-breakpoint-between(md, lg) {
    margin: map-get($spacers, 3) auto;
    max-width: 80%;
  }
  
  @include media-breakpoint-down(md) {
    margin: map-get($spacers, 3) 0;
    max-width: 100%;
  }
}

.leaderboard-title {
  @include font-size(2rem);
  color: var(--primary);
  margin: map-get($spacers, 5) 0;
  text-align: center;
  
  @include media-breakpoint-down(lg) {
    @include font-size(1.75rem);
    margin: map-get($spacers, 4) 0;
  }
  
  @include media-breakpoint-down(md) {
    @include font-size(1.5rem);
  }
}

.leaderboard-toggle-container {
  display: flex;
  justify-content: center;
  margin-bottom: map-get($spacers, 5);
  
  @include media-breakpoint-down(lg) {
    margin-bottom: map-get($spacers, 4);
  }

  .leaderboard-toggle {
    display: inline-flex;
    gap: map-get($spacers, 2);
    background: var(--card-bg);
    padding: map-get($spacers, 2);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow-sm);
    
    .btn {
      flex: 1;
      min-width: 140px;
      text-align: center;
      white-space: nowrap;
      
      @include media-breakpoint-between(md, lg) {
        min-width: 130px;
        padding-left: map-get($spacers, 2);
        padding-right: map-get($spacers, 2);
      }
      
      @include media-breakpoint-down(sm) {
        min-width: 120px;
      }
    }

    @include media-breakpoint-down(md) {
      width: 100%;
      max-width: 500px;
    }
  }
}

.leaderboard-table {
  th {
    background-color: var(--primary);
    color: var(--accent);
    position: sticky;
    top: 0;
  }

  td {
    padding: map-get($spacers, 2);
    
    @include media-breakpoint-down(md) {
      padding: map-get($spacers, 1);
    }
  }

  .btn-primary {
    @include button-variant($primary, $primary);
  }
  
  @include media-breakpoint-between(md, lg) {
    font-size: 0.9rem;
    
    .btn-sm {
      padding: 0.25rem 0.5rem;
      font-size: 0.8rem;
    }
  }
}

// Responsive table handling
@include media-breakpoint-down(lg) {
  .table-responsive {
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
  }
}

@include media-breakpoint-between(md, lg) {
  .container {
    width: 95%;
    max-width: 95%;
  }
}

@include media-breakpoint-down(md) {
  .friend-requests-container {
    max-height: none;
  }
  
  .leaderboard-toggle {
    flex-direction: row !important;
    
    .btn {
      flex: 1;
    }
  }
}

// Special ranks styling
.rank-gold td {
  background-color: rgba($accent, 0.1) !important;
  font-weight: $font-weight-bold;
}

.rank-silver td {
  background-color: rgba(#c0c0c0, 0.1) !important;
}

.rank-bronze td {
  background-color: rgba(#cd7f32, 0.1) !important;
}